.content{
    padding: 5px;
    border: 1px solid rgba(0,0,0,0.2);
    box-shadow: 5px 5px 15px rgba(0,0,0,0.2);
    border-radius: 5px;
    background: #edeef0;
}
.dialogsBody{
    display: grid;
    grid-template-columns: 2fr 10fr;
}
.dialogs_items{
    border-right: 1px solid rgba(0, 0, 0, 0.27);
}
.dialogs_items a{
    color: black;
    text-decoration: none;
}
.dialogs_items a.active{
    color: blue;
}

.dialog{
    width: 150px;
    padding-right: 15px;

}
.messages_items{

}
.messages_prim{
    display: flex;
    padding-left: 10px;
}
.message_enter_field{
padding: 20px 0 0 10px;
}
